import { Component, EventEmitter, Input, Output, ViewEncapsulation } from '@angular/core';
import { FormItemType } from '@xenon/layouts';

@Component({
  selector: 'xenon-text-form-field',
  templateUrl: './text-form-field.component.html',
  styleUrls: ['../form-item-type-component-styles.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class TextFormFieldComponent {
  @Input() label: string;
  @Output() labelChange: EventEmitter<string> = new EventEmitter();

  @Input() displayType: number;
  @Input() placeholder: string;
  formItemType = FormItemType;

  @Input() hasFiles: boolean;

  onChange(newLabel) {
    this.label = newLabel;
    this.labelChange.emit(this.label);
  }
}
