import { SelectionModel } from '@angular/cdk/collections';
import { Component, Inject, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatTableDataSource } from '@angular/material/table';
import { DriveFile } from '@xenon/api';

@Component({
  selector: 'xenon-form-uploads',
  templateUrl: './form-uploads.component.html',
  styleUrls: ['./form-uploads.component.scss'],
})
export class FormUploadsComponent implements OnInit {
  driveList: DriveFile[] = [];

  orgId: number;
  selectedId: number;
  formTemplateItemDriveId: number;
  selectedFile: DriveFile = null;
  blankFile = {
    ID: 0,
    Name: 'None',
    Description: '',
    Selected: false,
  };

  displayedColumns: string[] = ['id', 'name', 'desc'];
  dataSource = new MatTableDataSource<DriveFile>(this.driveList);
  selection = new SelectionModel<DriveFile>(true, []);

  result: {
    file: DriveFile;
    isFormTemplate: boolean;
  };

  constructor(public dialogRef: MatDialogRef<FormUploadsComponent>, @Inject(MAT_DIALOG_DATA) public dialogData: any) {
    if (dialogData.formTemplateItemId) {
      this.selectedId = dialogData.formTemplateItemId;
      this.orgId = dialogData.orgId;
      this.formTemplateItemDriveId = dialogData.formTemplateItemDriveId;
      this.initDriveList(dialogData.driveList);
    }
  }

  ngOnInit(): void {}

  initDriveList(driveList: DriveFile[]) {
    this.driveList = driveList;
    this.driveList.forEach((drive) => {
      drive.Selected = this.formTemplateItemDriveId === drive.ID ? true : false;
      if (drive.Selected) {
        this.selectFile(drive);
      }
    });

    this.dataSource = new MatTableDataSource(this.driveList);
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  selectFile(file: DriveFile) {
    let noFileSelected = false;

    if (this.selectedFile && this.selectedFile.ID === file.ID) {
      this.selectedFile = this.blankFile;
      noFileSelected = true;
    }

    this.driveList.forEach((drive) => {
      drive.Selected = false;
      if (!noFileSelected && drive.ID === file.ID) {
        this.selectedFile = drive;
        drive.Selected = true;
      }
    });
    this.dataSource = new MatTableDataSource(this.driveList);
  }

  save() {
    this.result = {
      file: this.selectedFile,
      isFormTemplate: this.dialogData.isFormTemplate,
    };
    this.dialogRef.close(this.result);
  }

  closeModal() {
    this.dialogRef.close();
  }
}
