import { Component, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { CdkDragDrop, transferArrayItem, CdkDragStart } from '@angular/cdk/drag-drop';
import { cloneDeep } from 'lodash-es';
import { FormBuilderService } from '@xenon/layouts';
import * as formItemModels from '@xenon/layouts'; //TODO change this to only get models
import { animate, state, style, transition, trigger } from '@angular/animations';
import { Observable, Subject } from 'rxjs';
import { ActivatedRoute, Router } from '@angular/router';
import { switchMap, takeUntil } from 'rxjs/operators';
import { ApiFormService, DriveFile, FormTemplate, FormTemplateItem } from '@xenon/api';
import { MatDialog } from '@angular/material/dialog';
import { FormUploadsComponent } from './form-uploads/form-uploads.component';
import { NotificationType, UiNotificationService } from '@xenon/ui';

@Component({
  selector: 'xenon-layout',
  templateUrl: './layout.component.html',
  styleUrls: ['./layout.component.scss'],
  animations: [
    trigger('slideInOut', [
      state(
        'opened',
        style({
          transform: 'translate3d(0,0,0)',
        })
      ),
      state(
        'closed',
        style({
          transform: 'translate3d(100%, 0, 0)',
        })
      ),
      transition('opened => closed', animate('300ms ease-in-out')),
      transition('closed => opened', animate('300ms ease-in-out')),
    ]),
  ],
  encapsulation: ViewEncapsulation.None,
})
export class LayoutComponent implements OnInit, OnDestroy {
  dragging = false;

  formItemType = formItemModels.FormItemType;

  defaultTextType: formItemModels.TextFormItem = {
    FormItemType: formItemModels.FormItemType.singleLineText.value,
    Label: formItemModels.ItemTypes.text.key,
    FormTemplateItemOptions: [],
    Required: false,
    DriveID: null,
    DriveFileName: '',
    selected: false,
    Order: 0,
    icon: formItemModels.FormItemType.singleLineText.icon,
    displayType: formItemModels.TextDisplayType.singleLine,
    Placeholder: '',
  };

  defaultNumberType: formItemModels.NumberFormItem = {
    FormItemType: formItemModels.FormItemType.number.value,
    Label: formItemModels.FormItemType.number.key,
    FormTemplateItemOptions: [],
    Required: false,
    DriveID: null,
    DriveFileName: '',
    selected: false,
    Order: 0,
    icon: formItemModels.FormItemType.number.icon,
    Units: '',
    Decimals: formItemModels.NumberDecimalPlacesType.zero,
    Placeholder: '',
  };

  defaultDateTimeType: formItemModels.DateTimeFormItem = {
    FormItemType: formItemModels.FormItemType.dateTime.value,
    Label: formItemModels.FormItemType.dateTime.key,
    FormTemplateItemOptions: [],
    Required: false,
    DriveID: null,
    DriveFileName: '',
    selected: false,
    Order: 0,
    icon: formItemModels.FormItemType.dateTime.icon,
    displayType: formItemModels.DateDisplayType.date,
  };

  defaultMultimediaType: formItemModels.MultimediaFormItem = {
    FormItemType: formItemModels.FormItemType.fileUpload.value,
    Label: formItemModels.ItemTypes.multimedia.key,
    FormTemplateItemOptions: [],
    Required: false,
    DriveID: null,
    DriveFileName: '',
    selected: false,
    Order: 0,
    icon: formItemModels.FormItemType.fileUpload.icon,
    camera: false,
    fileUpload: false,
    photoTypeAllowed: false,
    videoTypeAllowed: false,
    pdfTypeAllowed: false,
    documentAllowed: false,
    spreadsheetAllowed: false,
  };

  defaultMultipleChoiceType: formItemModels.MultipleChoiceFormItem = {
    FormItemType: formItemModels.FormItemType.checkbox.value,
    Label: formItemModels.ItemTypes.multipleChoice.key,
    FormTemplateItemOptions: [],
    Required: false,
    DriveID: null,
    DriveFileName: '',
    selected: false,
    Order: 0,
    icon: formItemModels.FormItemType.checkbox.icon,
    displayType: formItemModels.MultipleChoiceDisplayType.single,
    ResponseMin: formItemModels.ResponseCountType.zero,
    ResponseMax: formItemModels.ResponseCountType.zero,
  };

  itemTypes: formItemModels.FormItemType[] = [
    formItemModels.ItemTypes.text,
    formItemModels.FormItemType.number,
    formItemModels.FormItemType.dateTime,
    formItemModels.ItemTypes.multipleChoice,
    formItemModels.ItemTypes.multimedia,
  ];

  // formFieldsList: any[] = [];
  selectedField: any = null;

  textType = formItemModels.ItemTypes.text;
  numberType = formItemModels.FormItemType.number;
  dateTimeType = formItemModels.FormItemType.dateTime;
  multipleChoiceType = formItemModels.FormItemType.checkbox;
  multimediaType = formItemModels.FormItemType.fileUpload;

  requirementType: string[] = [formItemModels.RequirementType.required, formItemModels.RequirementType.optional];

  textDisplayType: string[] = [formItemModels.TextDisplayType.singleLine, formItemModels.TextDisplayType.multipleLine];

  numberDecimalPlacesType: number[] = [
    formItemModels.NumberDecimalPlacesType.zero,
    formItemModels.NumberDecimalPlacesType.one,
    formItemModels.NumberDecimalPlacesType.two,
    formItemModels.NumberDecimalPlacesType.three,
    formItemModels.NumberDecimalPlacesType.four,
    formItemModels.NumberDecimalPlacesType.five,
    formItemModels.NumberDecimalPlacesType.six,
  ];

  dateTimeDisplayType: string[] = [
    formItemModels.DateDisplayType.dateTime,
    formItemModels.DateDisplayType.date,
    formItemModels.DateDisplayType.time,
    formItemModels.DateDisplayType.dateTimeRange,
    formItemModels.DateDisplayType.dateRange,
    formItemModels.DateDisplayType.timeRange,
  ];

  multipleChoiceDisplayType: string[] = [formItemModels.MultipleChoiceDisplayType.single, formItemModels.MultipleChoiceDisplayType.multiple];

  multipleChoiceResponseType: number[] = [
    formItemModels.ResponseCountType.zero,
    formItemModels.ResponseCountType.one,
    formItemModels.ResponseCountType.two,
    formItemModels.ResponseCountType.three,
    formItemModels.ResponseCountType.four,
    formItemModels.ResponseCountType.five,
  ];

  itemSettingsDisplayState = 'closed';
  selectedForm: Observable<FormTemplate>;
  currentForm: FormTemplate;

  driveList: DriveFile[] = [];
  formTemplateDriveFileName = 'None';

  unsubscribe = new Subject<{}>();

  constructor(
    private formBuilderService: FormBuilderService,
    private router: Router,
    private route: ActivatedRoute,
    private apiFormService: ApiFormService,
    public dialog: MatDialog,
    public notificationService: UiNotificationService
  ) {}

  ngOnInit(): void {
    this.formBuilderService.currentForm
      .pipe(
        takeUntil(this.unsubscribe),
        switchMap((form) => {
          this.currentForm = form;
          return this.formBuilderService.currentDriveList;
        })
      )
      .subscribe((driveList) => {
        this.driveList = driveList;
        if (this.currentForm && this.currentForm.DriveID && this.driveList) {
          this.formTemplateDriveFileName = this.getDriveFilename(this.currentForm.DriveID);
        }
      });
  }

  ngOnDestroy() {
    this.unsubscribe.next();
    this.unsubscribe.complete();
  }

  navigate(path: string) {
    this.router.navigate([path], {
      relativeTo: this.route,
      queryParamsHandling: 'preserve',
    });
  }

  deselectSelectedClick(event) {
    if (event.target !== event.currentTarget) return;
    this.deselectSelectedField();
  }

  toggleItemSettings(open) {
    this.itemSettingsDisplayState = open ? 'opened' : 'closed';
  }

  selectField(field): void {
    if (field === this.selectedField) {
      return;
    }
    this.selectedField = field;
    this.deselectFormFields();
    field.selected = true;
    this.toggleItemSettings(true);
    if (this.selectedField.DriveID) {
      field.DriveFileName = this.getDriveFilename(this.selectedField.DriveID);
    } else {
      field.DriveFileName = 'None';
    }
  }

  getDriveFilename(driveId: number): string {
    let name = '';
    if (!driveId) {
      return name;
    }
    const itemDriveFile = this.driveList.find((drive) => drive.ID === driveId);

    if (itemDriveFile) {
      name = itemDriveFile.Name;
    }
    return name;
  }

  addFormItemOption(field: FormTemplateItem): void {
    if (!field.FormTemplateItemOptions) {
      field.FormTemplateItemOptions = [];
    }
    const itemNumber = field.FormTemplateItemOptions.length + 1;
    const newOption = {
      FormTemplateItemID: field.ID,
      Label: `Item ${itemNumber}`,
    };

    this.apiFormService.addItemOption(newOption).subscribe((response) => {
      const option = response.formItemOption;
      option.ID = response.ID;
      field.FormTemplateItemOptions.push(option);

      this.notificationService.show(NotificationType.success, 'Action done', 'Form template saved', 2500);
    });
  }

  deleteFieldFromList(): void {
    this.currentForm.FormTemplateItemList.splice(
      this.currentForm.FormTemplateItemList.findIndex((f) => f === this.selectedField),
      1
    );
    this.toggleItemSettings(false);
    this.deleteFormItem(this.selectedField.ID);
    this.selectedField = null;

    // this.formBuilderService.setLastSavedForm(this.currentForm);
  }

  deselectSelectedField(): void {
    if (!this.selectedField) {
      return;
    }
    this.selectedField.selected = false;
    this.selectedField = null;
    this.toggleItemSettings(false);
  }

  deselectFormFields(): void {
    this.currentForm.FormTemplateItemList.forEach((field) => {
      field.selected = false;
    });
  }

  dropped(event: CdkDragDrop<FormTemplateItem[]>): void {
    this.dragging = false;
    if (event.container.id === 'itemTypesList') {
      event.previousContainer.data.splice(event.previousIndex, 1);
    } else {
      if (event.previousContainer.id === 'itemTypesList') {
        if (event.previousContainer !== event.container) {
          this.addNewItemToForm(event);
        }
      } else {
        transferArrayItem(event.previousContainer.data, event.container.data, event.previousIndex, event.currentIndex);
      }
    }
    this.setItemsOrder();
  }

  addNewItemToForm(event: any) {
    this.itemTypes.splice(
      this.itemTypes.findIndex((it) => it === event.item.data),
      1
    );
    const newFieldDefault = this.getDefaultItemTypeObject(event.item.data);
    const newField = cloneDeep(newFieldDefault);
    newField.Order = event.currentIndex;
    if (!this.currentForm.FormTemplateItemList) {
      this.currentForm.FormTemplateItemList = [];
    }
    this.currentForm.FormTemplateItemList.splice(event.currentIndex, 0, newField);

    this.addFormTemplateItem(newField, event.currentIndex);
  }

  setItemsOrder() {
    for (let i = 0; i < this.currentForm.FormTemplateItemList.length; i++) {
      this.currentForm.FormTemplateItemList[i].Order = i;
    }
  }

  dragStart(event: CdkDragStart): void {
    const obj = this.itemTypes.findIndex((it) => it === event.source.data);
    this.itemTypes.splice(obj, 0, event.source.data);
    this.dragging = true;
  }

  addFormTemplateItem(newField: FormTemplateItem, itemIndex: number) {
    const formItem: FormTemplateItem = {
      FormTemplateID: this.currentForm.ID,
      FormItemType: newField.FormItemType,
      Decimals: formItemModels.NumberDecimalPlacesType.zero,
      DriveID: 0,
      Label: newField.Label,
      FormTemplateItemOptions: [],
      Order: itemIndex,
      MediaSourceAllow: 0,
      Placeholder: '',
      Required: false,
      Units: '',
    };

    this.apiFormService.addItem(formItem).subscribe((response) => {
      this.currentForm.FormTemplateItemList[itemIndex].ID = response.NewFormTemplateItem.ID;
      this.setItemsOrder();

      this.notificationService.show(NotificationType.success, 'Action done', 'Form template saved', 2500);
    });

    // this.formBuilderService.setLastSavedForm(this.currentForm);
  }

  deleteFormItem(formItemId: number) {
    this.apiFormService.deleteItem(formItemId).subscribe((response) => {
      this.notificationService.show(NotificationType.success, 'Action done', 'Form template item removed', 2500);
    });
  }

  getDefaultItemTypeObject(itemTypeObj): any {
    let returnItemTypeObj = {};
    switch (itemTypeObj.value) {
      case formItemModels.FormItemType.singleLineText.value:
        returnItemTypeObj = this.defaultTextType;
        break;

      case formItemModels.FormItemType.number.value:
        returnItemTypeObj = this.defaultNumberType;
        break;

      case formItemModels.FormItemType.dateTime.value:
        returnItemTypeObj = this.defaultDateTimeType;
        break;

      case formItemModels.FormItemType.fileUpload.value:
        returnItemTypeObj = this.defaultMultimediaType;
        break;

      case formItemModels.FormItemType.checkbox.value:
        returnItemTypeObj = this.defaultMultipleChoiceType;
        break;

      default:
        returnItemTypeObj = this.defaultTextType;
        break;
    }
    return returnItemTypeObj;
  }

  selectDriveFile(item: FormTemplateItem | FormTemplate) {
    if (!item) {
      return;
    }

    let id = this.currentForm.ID;
    let isFormTemplate = false;

    if (item.ID === this.currentForm.ID) {
      id = this.currentForm.ID;
      isFormTemplate = true;
    } else if (this.selectedField) {
      id = this.selectedField.ID;
    }

    const dialogRef = this.dialog.open(FormUploadsComponent, {
      data: {
        formTemplateItemId: id,
        orgId: this.currentForm.OrgID,
        formTemplateItemDriveId: item.DriveID,
        driveList: this.driveList,
        isFormTemplate: isFormTemplate,
      },
      panelClass: 'drive-file-dialog',
    });

    dialogRef.afterClosed().subscribe((result) => {
      if (!result) {
        return;
      }

      if (result.isFormTemplate) {
        this.currentForm.DriveID = result.file.ID;
        this.formTemplateDriveFileName = result.file.Name;
      } else {
        this.selectedField.DriveID = result.file.ID;
        this.selectedField.DriveFileName = result.file.Name;
      }
    });
  }
}
