import { Component, Input, OnChanges, SimpleChanges } from '@angular/core';
import { FormItemType } from '@xenon/layouts';
import * as _ from 'lodash-es';

@Component({
  selector: 'xenon-label-field',
  templateUrl: './label-field.component.html',
  styleUrls: ['./label-field.component.scss'],
})
export class LabelFieldComponent implements OnChanges {
  @Input() header: string;
  @Input() label: string;
  @Input() type: number;
  @Input() icon: string;
  iconName: string;
  formItemType = FormItemType;

  ngOnChanges(changes: SimpleChanges) {
    if (changes.type && changes.type.currentValue) {
      const type = _.findKey(this.formItemType, { value: this.type });

      if (!this.icon) {
        this.iconName = this.formItemType[type].icon;
      } else {
        this.iconName = this.icon;
      }
    }
  }
}
