import { Component, EventEmitter, Input, Output, ViewEncapsulation } from '@angular/core';
import { ApiFormService, FormTemplateItemOption } from '@xenon/api';
import { MultipleChoiceDisplayType } from '@xenon/layouts';

@Component({
  selector: 'xenon-multiple-choice-form-field',
  templateUrl: './multiple-choice-form-field.component.html',
  styleUrls: ['../form-item-type-component-styles.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class MultipleChoiceFormFieldComponent {
  @Input() label: string;
  @Output() labelChange: EventEmitter<string> = new EventEmitter();

  @Input() itemType: number;
  @Input() itemId: number;
  @Input() displayType: number;
  @Input() itemOptions: FormTemplateItemOption[];
  multipleChoiceDisplayType = MultipleChoiceDisplayType;

  @Output() addOption: EventEmitter<FormTemplateItemOption> = new EventEmitter();

  @Input() hasFiles: boolean;

  constructor(private apiFormService: ApiFormService) {}

  addFormItemOption(): void {
    this.addOption.emit();
  }

  deleteFormItemOption(optionToRemove): void {
    this.apiFormService.deleteItemOption(optionToRemove).subscribe((response) => {
      const index = this.itemOptions.indexOf(optionToRemove);
      this.itemOptions.splice(index, 1);
    });
  }

  trackByIndex(index: number): any {
    return index;
  }

  onChange(newLabel) {
    this.label = newLabel;
    this.labelChange.emit(this.label);
  }
}
