import { Component, EventEmitter, Input, Output, ViewEncapsulation } from '@angular/core';

@Component({
  selector: 'xenon-multimedia-form-field',
  templateUrl: './multimedia-form-field.component.html',
  styleUrls: ['../form-item-type-component-styles.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class MultimediaFormFieldComponent {
  @Input() label: string;
  @Output() labelChange: EventEmitter<string> = new EventEmitter();

  @Input() hasFiles: boolean;

  // @Input() displayType: TextDisplayType;
  // @Input() placeholderText: string;
  // textDisplayType = TextDisplayType;

  onChange(newLabel) {
    this.label = newLabel;
    this.labelChange.emit(this.label);
  }
}
