import { Component, EventEmitter, Input, Output, ViewEncapsulation } from '@angular/core';
import { DateDisplayType } from '@xenon/layouts';

@Component({
  selector: 'xenon-date-time-form-field',
  templateUrl: './date-time-form-field.component.html',
  styleUrls: ['../form-item-type-component-styles.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class DateTimeFormFieldComponent {
  @Input() label: string;
  @Output() labelChange: EventEmitter<string> = new EventEmitter();

  @Input() displayType: number;
  dateTimeDisplayType = DateDisplayType;

  timePlaceholder = '00:00 --';

  @Input() hasFiles: boolean;

  onChange(newLabel) {
    this.label = newLabel;
    this.labelChange.emit(this.label);
  }
}
